#dropbox {
  display: flex;
  height: 20px;
}

#arrow {
  margin-left: -15px;
  cursor: pointer;
  display: inline-block;
}

#box {
  padding-left: 5px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
#label {
  font-size: 12px;
  color: rgba(172, 172, 172) !important;
}

#drop {
  display: flex;
  flex-direction: column;
  position: relative;
}

#boxarea {
  box-shadow: 0px 8px 16px -2px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  max-height: 300px;
  overflow-y: scroll;
  padding-top: 5px;
  position: absolute;
  top: 21px;
  z-index: 99;
}
#boxarea::-webkit-scrollbar {
  width: 0.3em;
}

#boxarea::-webkit-scrollbar-track {
  box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.13);
  border-radius: 10px;
}

#boxarea::-webkit-scrollbar-thumb {
  background-color: #4cb281;
  outline: 1px solid #4cb281;
}

.listarea {
  margin-left: 3px;
  margin-top: -3px;
  padding-bottom: 3px;
}

#searchlist {
  border: none;
  margin-left: 5px;
  margin-bottom: 3px;
  border-radius: 5px;
  width: 90%;
  font-size: 11px;
  height: 18px;
  padding-left: 2px;
}

#searchlist:focus {
  outline: none;
}

#searchlist::placeholder {
  font-size: 11px;
}
